'use client';

import React from 'react';
import { motion } from 'framer-motion';
import { 
  Sparkles, Zap, ShieldCheck, Moon, 
  Droplet, Trash2, Calendar, Clock, DollarSign
} from 'lucide-react';

const serviceIcons = {
  routine: <Sparkles size={24} />,
  rapid: <Zap size={24} />,
  deep: <Droplet size={24} />,
  compliance: <ShieldCheck size={24} />,
  night: <Moon size={24} />,
  addon: <Trash2 size={24} />,
};

const serviceColors = {
  routine: 'from-blue-500 to-blue-600',
  rapid: 'from-red-500 to-orange-600',
  deep: 'from-purple-500 to-purple-600',
  compliance: 'from-green-500 to-emerald-600',
  night: 'from-indigo-500 to-indigo-600',
  addon: 'from-gray-500 to-gray-600',
};

export default function ServiceCard({ service, index = 0 }) {
  const { name, type, description, base_price, pricing_model, duration_hours } = service;

  return (
    <motion.div
      initial={{ opacity: 0, y: 30 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ delay: index * 0.1, duration: 0.6 }}
      whileHover={{ y: -8, scale: 1.02 }}
      className="group relative p-8 rounded-3xl bg-white/5 border border-white/10 backdrop-blur-sm hover:bg-white/10 hover:border-white/20 transition-all duration-300"
    >
      {/* Gradient Background */}
      <div className={`absolute inset-0 bg-gradient-to-br ${serviceColors[type] || 'from-blue-500 to-blue-600'} opacity-0 group-hover:opacity-10 rounded-3xl transition-opacity duration-300`}></div>
      
      <div className="relative z-10 space-y-6">
        {/* Icon */}
        <div className={`w-16 h-16 rounded-2xl bg-gradient-to-br ${serviceColors[type] || 'from-blue-500 to-blue-600'} flex items-center justify-center text-white shadow-lg group-hover:scale-110 transition-transform duration-300`}>
          {serviceIcons[type] || <Sparkles size={24} />}
        </div>

        {/* Content */}
        <div className="space-y-3">
          <h3 className="text-2xl font-black uppercase tracking-tight text-white group-hover:text-white transition-colors">
            {name}
          </h3>
          
          <p className="text-sm text-gray-400 leading-relaxed min-h-[3rem]">
            {description}
          </p>

          {/* Details */}
          <div className="flex flex-wrap gap-4 pt-2">
            {base_price > 0 && (
              <div className="flex items-center gap-2 text-gray-300">
                <DollarSign size={14} className="text-green-500" />
                <span className="text-xs font-bold">
                  {pricing_model === 'hourly' ? `₦${base_price.toLocaleString()}/hr` : 
                   pricing_model === 'per_sqm' ? `₦${base_price.toLocaleString()}/m²` : 
                   `₦${base_price.toLocaleString()}`}
                </span>
              </div>
            )}
            
            {duration_hours && (
              <div className="flex items-center gap-2 text-gray-300">
                <Clock size={14} className="text-blue-500" />
                <span className="text-xs font-bold">{duration_hours}hrs</span>
              </div>
            )}

            <div className="flex items-center gap-2 text-gray-300">
              <Calendar size={14} className="text-purple-500" />
              <span className="text-xs font-bold uppercase tracking-wider">{type}</span>
            </div>
          </div>
        </div>

        {/* CTA Arrow */}
        <div className="flex items-center gap-2 text-gray-400 group-hover:text-white transition-colors">
          <span className="text-xs font-bold uppercase tracking-wider">Learn More</span>
          <motion.div
            animate={{ x: [0, 5, 0] }}
            transition={{ duration: 1.5, repeat: Infinity, ease: "easeInOut" }}
            className="opacity-0 group-hover:opacity-100 transition-opacity"
          >
            →
          </motion.div>
        </div>
      </div>
    </motion.div>
  );
}
